import React, { useState, useEffect } from 'react';
import { Box, Typography, CircularProgress, Paper, useTheme } from '@mui/material';

// Fun facts about imports to Israel
const funFacts = [
  "?  2024   65,069  .     ",
  "?  2024   39  .       '.",
  "?        ,  370,422 .  ,     !",
  "?        ,    .      .",
  "?  2024   25,696  . , ?",
  "?  2024   48,149  .         .",
  "?  2024   2,355  .      "
];

function Loader() {
  // Start with a random fact
  const [factIndex, setFactIndex] = useState(Math.floor(Math.random() * funFacts.length));
  const theme = useTheme();
  const isDarkMode = theme.palette.mode === 'dark';
  
  // Change the fun fact every 5 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      setFactIndex((prevIndex) => (prevIndex + 1) % funFacts.length);
    }, 5000);
    
    return () => clearInterval(interval);
  }, []);
  
  return (
    <Box 
      sx={{ 
        display: 'flex', 
        flexDirection: 'column',
        justifyContent: 'center', 
        alignItems: 'center', 
        height: '100vh',
        background: isDarkMode
          ? 'linear-gradient(135deg, #1a1a1a 0%, #2c3e50 100%)' 
          : 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)',
        transition: 'background 0.3s'
      }}
    >
      <Paper 
        elevation={3} 
        sx={{ 
          p: 4, 
          borderRadius: 2, 
          maxWidth: '600px',
          textAlign: 'center',
          background: isDarkMode
            ? 'rgba(30, 30, 30, 0.9)' 
            : 'rgba(255, 255, 255, 0.9)',
          transition: 'background 0.3s'
        }}
      >
        <Typography variant="h4" component="h1" gutterBottom sx={{ color: theme.palette.primary.main }}>
               2024...
        </Typography>
        
        <CircularProgress size={60} sx={{ my: 3 }} />
        
        <Box sx={{ 
          mt: 3, 
          p: 2, 
          bgcolor: isDarkMode ? 'rgba(0, 0, 0, 0.2)' : '#f5f5f5', 
          borderRadius: 1,
          transition: 'background-color 0.3s'
        }}>
          <Typography variant="body1" sx={{ fontStyle: 'italic' }}>
            {funFacts[factIndex]}
          </Typography>
        </Box>
      </Paper>
    </Box>
  );
}

export default Loader;
